<div class="classgrade"><div class="left" v-if="isAdmin === &quot;admin&quot;"><div class="head"><span class="span1">管理员</span></div><a-list class="teacherlist" :data-source="teacherList" rowKey="code" :split="false" :loading="loading" :loadingMore="loadingMore" :showLoadingMore="showLoadingMore"><template v-slot:renderItem="{ item, index }"><a-list-item class="btn" @click="changeList(index, item)" :class="{ selected: index == checkindex }">{{ item.name }}</a-list-item></template><template #loadMore><div v-if="showLoadingMore" :style="{ textAlign: &quot;center&quot;, marginTop: &quot;12px&quot;, height: &quot;32px&quot;, lineHeight: &quot;32px&quot; }"><a-spin v-if="loadingMore"></a-spin></div></template></a-list></div><div class="right" :class="{ &quot;right-admin&quot;: isAdmin === &quot;admin&quot;, &quot;right-teacher&quot;: isAdmin === &quot;teacher&quot; }"><div class="head"><div class="left-btns"><a-button class="link-btn" type="link" @click="showVisibleHelp">帮助提示<div class="q-tip">？</div></a-button></div></div><div class="line"></div><div class="body"><a-table :columns="columns" v-model:dataSource="data" :loading="queryListLoding" :pagination="pagination" rowKey="id" :scroll="{ x: scrollx }"><template v-slot:studenttotal="{ text, record }"><a-button class="btn-link" type="link" @click="goToInfo(record)">{{ text }}</a-button></template><template v-slot:action="{ text, record }"><a @click="download(record)">下载详情</a></template></a-table></div><a-modal title="帮助提示" :visible="modalVisibleHelp" @cancel="handleCancelHelp" :footer="null" centered :width="480" :bodyStyle="{ height: &quot;200px&quot; }"><div>1、班级成绩为该班级下所有学生在平台学习的平均成绩；</div><div>2、系统每日23时50分更新当日成绩；</div><div>3、用户可前往成绩统计查看可视化图表；</div></a-modal></div></div>